
.body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #7A6B6B;
}

h2 {
  font-size: 1.5em;
}

.mbt {
  margin-bottom: 25px;
}

.addnavbarstyle {
  margin-bottom: 15px;
  background-color: #009688!important;
  color: black!important;
  height: 49px;
  border-radius: 7px;
}

.addli {
  margin-left: 30px;
  color: #000;
}

a {
  color: #000!important;
  text-decoration: none;
  line-height: 1;
  display: inline-block;
}

a:hover:after,
a:focus:after {
    width: 100%;
}

a:after {
  display: block;
  content: "";
  height: 1px;
  width: 0%;
  background-color: #000;
  -webkit-transition: width .3s ease-in-out;
  -o-transition: width .3s ease-in-out;
  transition: width .3s ease-in-out;
}

.linkportfolio a {
  text-decoration: underline!important;
}

.linkportfolio a:hover:after,
.linkportfolio a:focus:after {
    width: 0%;
}

.linkportfolio a:hover {
  color: #009688!important;
}

