.linkfooter a {
  color: #fff!important;
}

.w3-xxlarge {
  font-size: 27px!important;
}

.w3-teal {
  background-color: #000!important;
}

.plang {
  margin-bottom: 0px!important;
}

.lwidth {
  width: 76%!important;
}


